<template>
	<!-- 轮播图组件 -->
	<div class="banner">
		<transition-group tag="ul" enter-active-class="animate__animated animate__flipInX" leave-active-class="animate__animated animate__flipOutX">
			<li v-for="(item, index) in picurl" v-show="index==curIndex" @transitionend="end" @mousemove="move" @mouseout="out" :key="index"><img :src="item" alt=""/></li>
		</transition-group>
		<ol>
			<li :class="k==curIndex?'active':''" v-for="(c, k) in picurl.length" :key="k" @click="clickDot(k)"></li>
		</ol>
	</div>
</template>

<script>
	export default{
		data(){
			return {
				picurl:[
					["http://p1.music.126.net/rSzcSzMpiD_A65Cc6bMq7w==/109951165823303749.jpg?imageView&quality=89"],
					["http://p1.music.126.net/ah9CWGlffEO91ELyrKoftw==/109951165824581619.jpg?imageView&quality=89"],
					["http://p1.music.126.net/Tx_necPnFBCuKMrosHJXTw==/109951165824294690.jpg?imageView&quality=89"],
					["http://p1.music.126.net/hZpzFhr8yZF8IrR0illZdA==/109951165823325049.jpg?imageView&quality=89"],
					["http://p1.music.126.net/3cm3JLy7DUnl-YtySirC1g==/109951165670392962.jpg?imageView&quality=89"]
				],
				curIndex:0,//图片播放下标
				timer:null,
				flag:true//节流阀
			}
		},
		methods:{
			clickDot(value){
				this.move();
				this.curIndex=value;
				this.end();
			},
			move(){
				clearInterval(this.timer);
			},
			end(){
				if(this.flag){
					this.flag=false;
					this.timer = setInterval(function(){
							this.curIndex++;
							if(this.curIndex > this.picurl.length-1){
								this.curIndex = 0;
							}
							this.flag=true;
					}.bind(this),2500);
				}
			},
			out(){
				this.end();
			}
		},
		mounted() {
			this.end();
		},
	}
</script>

<style lang="less" scoped="scoped">
	.banner{
		width: 90%;
		height: 20vh;
		margin: 15px auto 0;
		position: relative;
		overflow: hidden;
		ul{
			width: 1000%;
			height: 100%;
			li{
				width: 100%;
				position: absolute;
				box-shadow: 5px 3px 12px rgba(0,0,0,.5);
			}
		}
		span{
			position: absolute;
			top: 50%;
			font-size: 30px;
			color: #999;
			font-weight: bold;
			transform: translateY(-50%);
		}
		ol{
			width: 70px;
			height: 9px;
			position: absolute;
			left: 50%;
			bottom: 15px;
			transform: translateX(-50%);
			list-style: none;
			li{
				border-radius: 50%;
				border: 1px solid #999;
				box-sizing: border-box;
				float: left;
				width: 9px;
				height: 9px;
				background-color: white;
				margin: 0 2.5px;
			}
			.active{
				background-color: #dd001b;
			}
		}
	}
</style>
