<template>
        <!--  MV  -->
     <div>
        <div class="mv">
            <div class="banner">
            <Banner></Banner>
            </div>
            <div class="mvlist">
            <Mvitem v-for="(m,index) in MvList" :key="index" :item="m"></MvItem>
            </div>
            <Loading v-if="showLoading"></Loading>
            <div class="loadingmore">{{ momore ? "没有更多了" : "正在加载..." }}</div>
        </div>
     </div>
</template>
<script>
import Banner from '@/components/Banner'
import Mvitem from '@/components/Mvitem'
import Loading from '@/components/Loading'
export default {
    name:"moveradio",
    data(){
        return{
            MvList:[],
            showLoading:true,
            page:1,
            momore:false
        }
    },
    components:{
        Banner,
        Mvitem,
        Loading
    },
    created(){//获取全部mv
        this.loadSearch();
        window.addEventListener("scroll",this.handleScroll);
    },
    methods:{
        loadSearch(){
            //每页显示多少条offset 偏移(page-1)*30
            //根据关键字获取搜索结果
            let offset=(this.page-1)*30;
            this.$axios.get('/mv/all?type=1&offset='+offset).then(data=>{
                this.MvList= this.MvList.concat(data.data.data);
                this.showLoading=true
            });
        },

        handleScroll () { //滚动事件监听处理
            //是否滚动到底部的判断
            let scrolltop = document.documentElement.scrollTop || document.body.scrollTop || window.pageYOffset;
            if (scrolltop + document.documentElement.clientHeight == document.documentElement.scrollHeight) {
                this.page ++;
                if (this.page>6) {
                    //如果大于总页数停止请求数据
                    this.momore = true;
                    this.showLoading = false;
                    return false;
                } else {
                    this.loadSearch();
                    this.showLoading = true;
                }
            }
        },
        destoryed(){
            window.removeEventListener("scroll",this.handleScroll,false)
        },
    }
}
</script>
<style lang="less" scoped>
.mv{
    width: 100%;
    height: 100%;
    .banner{
        width: 100%;
    }
    .mvlist{
        display: flex;
        flex-direction: column;
        margin-top: 15px;
        padding: 0 10px;
    }
    .loadingmore{
        color: #d43c33;
        text-align: center;
    }
}
</style>